import React, { Component } from "react";

class Portfolio extends Component {
  render() {
    if (this.props.data) {
      var projects = this.props.data.map((project) => {
        var projectImage = "images/projects/" + project.icon;
        return (
          <div key={project.name} className="columns portfolio-item">
            <div className="item-wrap">
              <img
                className="work-pic"
                alt={project.name}
                src={projectImage}
              />
              <div className="overlay">
                <div className="portfolio-item-meta">
                  <h5>{project.name}</h5>
                  <p>{project.description}</p>
                </div>
              </div>
            </div>
          </div>
        );
      });
    }

    return (
      <section id="portfolio">
        <div className="row">
          <div className="twelve columns collapsed">
            <h1>
              Check Out Some of the Apps that I've Developed Alone or
              Collaboratively.
            </h1>
            {projects}
          </div>
        </div>
      </section>
    );
  }
}

export default Portfolio;
